@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

body{
    font-family: 'Nanum Gothic', sans-serif;
    background-color: #3A3A3A;
    color: white;
}

.container {
    width: 80%;
    margin: auto;
}

.Nav{
    width: 100%;
    background-color: #107C10;
    color: white;
    height: 70px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.Search{
    margin-top: 40px;
    text-align: center; 
}

.Search form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Search input{
    width: 50%;
    height: 3rem;
    font-size: 1.5rem;
    text-align: center;
    transition: 0.7s ease-in-out;
}

.Search input:focus{
    width: 80%;
    background-color: #3A3A3A;
    color: white;
}

.Search button{
    width: 8rem;
    height: 40px;
    margin-top: 1rem;
    font-size: medium;
    cursor: pointer;
    border: 3px solid #107C10;
    background: none;
    color: white;
    border-radius: 10px;

}

.Search button:hover{
    background: #107C10;
}

.heading{
    text-align: center;
    margin-top: 30px;
}

.Grid{
    margin-top: 60px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-flow: row;
    grid-gap: 10px;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.card{
    background-color: #3A3A3A;
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    height: 140px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}

.card:hover{
    color: #107C10;
}

.card button{
    width: 5rem;
    height: 30px;
    margin-top: 10px;
    color: white;
    background-color: #3A3A3A;
}
.links{
    color: white;
    text-decoration: none;
}

.LyricsPage{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 70%;
    margin: auto;
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    margin-top: 100px;
    background-color: #3A3A3A;
    color: white;
}
.LyricsPage h3{
    margin-top: 10px;
}

.LyricsPage p{
    margin: 30px 20px;
}

@media only screen and (max-width:800px){
    .container{
        width: 100%;
        display: block;
    }

    .Grid{
        display: block;
        text-align: center;
        width: 80%;
        margin: auto;
        padding: 20px;
    }

    .card{
        margin:30px;
    }
}